/* Reset padding and margin for all elements */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
/* Main Container */
.main-containers {
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  /* flex-direction: column; */
  flex-wrap: wrap;
  height: 100vh;
  width: 100%;
}

/* Left Content Container */
.content-container {
  width: 70%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 20px;
  padding: 100px 10% 0;
}

.content-container h1 {
  font-size: 50px;
  margin: 0;
}

.content-container p {
  text-align: justify;
  font-size: 20px;
  margin: 0;
}

.content-btn {
  display: flex;
  gap: 15px;
}

.content-container button {
  cursor: pointer;
  border: none;
  font-size: 22px;
  height: 60px;
  width: 150px;
  background-color: #333;
  color: white;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.content-container button:hover {
  box-shadow: rgba(8, 8, 8, 0.25) 0px 30px 60px -12px inset,
              rgba(8, 8, 8, 0.3) 0px 18px 36px -18px inset;
  transform: translateY(-2px);
}

/* Right Content Container */
.content-container-2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
}

.content-container-2 img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

/* Responsive Design */

/* Tablet View */
@media (max-width: 768px) {
  .main-containers {
    flex-direction:column-reverse;
    height: auto;
  }

  .content-container,
  .content-container-2 {
    width: 100%;
    padding: 20px;
  }
  /* .content-container {
    padding-top: 120px;
  } */

  .content-container h1 {
    font-size: 40px;
    /* width: 100%; */
    text-align: left;
  }

  .content-container p {
    font-size: 18px;
    text-align:left;
  }

  .content-container button {
    font-size: 18px;
    height: 50px;
    width: 130px;
  }
  .content-btn{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .content-container-2 img {
    margin-top: 0;
    object-fit: contain;
  }
}

/* Mobile View */
@media (max-width: 480px) {
  .main-containers {
    flex-direction: column-reverse;
    height: auto;
  }

  .content-container {
    text-align: center;
  }

  .content-container h1 {
    font-size: 32px;
  }
  .content-container p {
    font-size: 16px;
    text-align: justify;
  }

  .content-container button {
    font-size: 16px;
    height: 45px;
    width: 120px;
  }

  .content-btn {
    gap: 10px;
    justify-content: center;
  }

  .content-container-2 img {
    margin: 0 auto;
    width: 90%;
    object-fit: contain;
  }
}

/* Large Screens */
@media (min-width: 1024px) {
  .main-containers {
    flex-direction: row;
    height: 100vh;
  }

  .content-container h1 {
    font-size: 55px;
  }

  .content-container p {
    font-size: 22px;
  }

  .content-container button {
    font-size: 20px;
    height: 60px;
    width: 150px;
  }
}
