/* Container styling */
.student-details {
    padding: 80px 20px 20px 20px;
    width: 100%;
    margin: 0 auto;
    font-family: Arial, sans-serif;
    /* background-color: #f9f9f9; */
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Table styling */
table {
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

th, td {
    padding: 15px;
    text-align: center;
    border: 1px solid #ddd;
    font-size: 14px;
}

th {
    background-color: transparent;
    /* color: ; */
}

td {
    background-color: transparent;
}

.t1, td {
    text-align: center;
}

/* Button styling */
.delete-button {
    padding: 8px 15px;
    background-color: #4CAF50;
    color: black;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.delete-button:hover {
    background-color: #45a049;
}

/* Edit and Cancel Buttons */
.save-button {
    background-color: #4CAF50;
}

.cancel-button {
    background-color: #f44336;
}

.cancel-button:hover {
    background-color: #e53935;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
    .student-details{
        overflow: auto;
    }
    table {
        font-size: 12px;
    }

    th, td {
        padding: 10px;
    }

    button {
        padding: 5px 10px;
        font-size: 12px;
    }

    .student-details {
        padding: 90px 15px 15px 15px;
    }
}

@media screen and (max-width: 480px) {
    .student-details{
        padding: 80px 20px 20px 20px;
        overflow: auto;
    }
    table, th, td {
        /* display: block; */
        /* justify-content: center; */
        width: 100%;
    }

    th, td {
        text-align: left;
        padding: 10px;
    }

    th {
        background-color: #333;
        color: white;
    }

    tr {
        margin-bottom: 10px;
    }

    .delete-button {
        width: 100%;
        padding: 10px;
        font-size: 14px;
    }
}

/* Modal Styling */
.edit-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: white;
    border-radius: 8px;
    padding: 20px;
    z-index: 1000;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    max-width: 400px;
    width: 90%;
}

.edit-form label {
    display: block;
    margin: 10px 0 5px;
    font-weight: bold;
}

.edit-form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.save-button, .cancel-button {
    width: 48%;
    display: inline-block;
    text-align: center;
}

.save-button:hover, .cancel-button:hover {
    background-color: #3e8e41;
}

.cancel-button:hover {
    background-color: #d32f2f;
}
